/*
    Elements: forms
    ---
    Default markup for the form elements
*/

legend,
label {
    display: block;
    width: 100%;
    cursor: pointer;
    @include text-dimensions($alpha-font-size,1,0,0,.25);
}

// Input elements

input[type="input"],
input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="url"],
input[type="number"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[name="field_country_code"],
input[name="field_city_code"],
input[name="field_phone_int"],
textarea {
    -webkit-appearance: none;
    -moz-appearance:    none;
    -ms-appearance:     none;
    appearance:         none;
    font-smoothing: antialiased;
    background-color: $delta-ui-color;
    border-radius: $radius;
    border: $border-width $border-style $gamma-ui-color;
    color: lighten($alpha-text-color, 30);
    font-family: $alpha-font-family;
    margin: 0;
    width: 100%;
    display: block;
    outline: none;
    line-height: normal;
    border-radius: $radius;
    @include input-md;

    &:focus {
        color: $alpha-text-color;
        border-color: $alpha-ui-color;
        box-shadow: 0 0 2px rgba($alpha-ui-color,.5);
    }
}

textarea {
    height: auto;
    resize: none;
}

// Select, depends on the project. Use a custom select plugin to properly style these.

select,
select[multiple] {

}

// Placeholders

::-webkit-input-placeholder {
    font-family: $beta-font-family;
    font-style: italic;
}

::-moz-placeholder {
    font-family: $beta-font-family;
    font-style: italic;
}

:-ms-input-placeholder {
    font-family: $beta-font-family;
    font-style: italic;
}
