/*
    Component: Icons
    ---
    Icon styling
*/

@mixin icon {
    display: inline-block;
    position: relative;
    background-repeat: no-repeat;
    background-position: center top;

    @include hide-text;
}

// ---
// Modifiers
// ---
.c-icon--menu {

    top: 5px;
    width: 50px;
    height: 30px;
    background-image: url('../img/svg/c-icon--menu.svg');
    background-size: 20px auto;

    @include icon;
}

.c-icon--close {

    top: 5px;
    width: 50px;
    height: 30px;
    background-image: url('../img/svg/c-icon--close.svg');
    background-size: 20px auto;

    @include icon;
}

.c-icon--twitter {
    opacity: .7;
    &:hover {
      opacity: 1;
    }

    top: 5px;
    width: 30px;
    height: 30px;
    background-image: url('../img/svg/c-icon--twitter.svg');
    background-size: 20px auto;

    @include icon;
}

